import {AddAddress} from '../../common/api.js'
export default {
	data() {
		return {
			formData:{
				real_name:"",
				phone:"",
				address:"",
				province:"",
				city:"",
				area:""
			},
			type:'a',
			region: [],
		}
	},
	onLoad(option){
		this.type = option.type;
	},
	methods: {
		bindRegionChange(e) {
		    // // console.log('picker发送选择改变，携带值为', e.detail.value)
		      this.region = e.detail.value;
		},
		saveAddress(){
			let _this = this;
			this.formData.province = this.region[0];
			this.formData.city = this.region[1];
			this.formData.area=this.region[2];
			if(this.formData.real_name == ''){
				uni.showToast({
					icon:"none",
				    title: '收货人未填写'
				});
			}else 
			if(this.formData.phone == ''){
				uni.showToast({
					icon:"none",
				    title: '联系电话未填写'
				});
			}else
			if(this.formData.address == ''){
				uni.showToast({
					icon:"none",
				    title: '详细地址未填写'
				});
			}else if(this.region.length<1){
				uni.showToast({
					icon:"none",
				    title: '所在地区未选择'
				});
			}else{
				AddAddress(this.formData).then(res=>{
					if(res.status){
						uni.showToast({
							icon:"success",
						    title: '创建成功',
							success:function(){
								if(_this.type=='a'){
									uni.navigateTo({
									    url: '../address/address'
									});
								}else{
									uni.navigateTo({
									    url: '../chooseAddresss/chooseAddresss'
									});
								}
								
							}
						});
						
					}else{
						uni.showToast({
							icon:"none",
						    title: '创建失败，请稍后重试'
						});
					}
				})
			}
			
		}
		  
	}
}
